<template>
  <div class="feedback">
    <div style="display: flex; justify-content: space-between;">
      <h3 style="margin-bottom:40px">{{label}}</h3>
      <el-button type="primary" size="small" style="height: 30px;" @click="$router.push({ path: 'overview' })">返回
      </el-button>
    </div>

    <div>
      <div style="margin-top:10px" v-if="tab=='1'&&label!='总走访人数'">
        <add-people-table title="寄住信息" :data="lodgePersonsTable"></add-people-table>
      </div>
      <div style="margin-top:10px"  v-if="tab=='2'||label=='总走访人数'">
        <add-pie-table title="寄住信息" :data="lodgePersonsTable"></add-pie-table>
      </div>
    </div>


  </div>
</template>

<script>
    import Service from '../service.js'
    import AddPeopleTable from './add-people-table'
    import AddPieTable from './add-pie-table'

    export default {
        components: { AddPeopleTable,AddPieTable },
        props: [],
        data() {
            return {
                tab:"",
                type: "",
                label:"",
                lodgePersonsTable: []
            }
        },
        methods: {
            getServer (type) {
                Service.getItemNewCardList(type).then(res => {
                    if (!res.data) return
                    if (!res.data.allData) return
                    console.log(res.data.allData)
                    this.lodgePersonsTable = res.data.allData ? res.data.allData : []
                })
            }
        },
        computed: {},
        mounted() {
            this.tab=this.$route.query.tab;
            this.label=this.$route.query.type;
            this.getServer(this.$route.query)
        }
    }
</script>

